import React from 'react';
import { Link, useNavigate } from 'react-router-dom';

const Navbar = ({ user, setUser }) => {
  const navigate = useNavigate();

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    setUser(null);
    navigate('/login');
  };

  return (
    <nav style={{ 
      background: '#1f2937', 
      color: 'white', 
      padding: '1rem 0',
      marginBottom: '2rem'
    }}>
      <div className="container flex justify-between items-center">
        <Link to="/" style={{ 
          color: 'white', 
          textDecoration: 'none', 
          fontSize: '1.5rem',
          fontWeight: 'bold'
        }}>
          EquipShare
        </Link>
        {user && (
          <div className="flex gap-2 items-center">
            <Link to="/equipment" style={{ color: 'white', textDecoration: 'none', marginRight: '1rem' }}>
              Equipment
            </Link>
            <Link to="/requests" style={{ color: 'white', textDecoration: 'none', marginRight: '1rem' }}>
              Requests
            </Link>
            {(user.role === 'admin' || user.role === 'staff') && (
              <Link to="/admin" style={{ color: 'white', textDecoration: 'none', marginRight: '1rem' }}>
                Admin
              </Link>
            )}
            <span style={{ marginRight: '1rem', fontSize: '0.875rem' }}>
              {user.name} ({user.role})
            </span>
            <button onClick={handleLogout} className="btn btn-secondary">
              Logout
            </button>
          </div>
        )}
      </div>
    </nav>
  );
};

export default Navbar;
