import React, { useState, useEffect } from 'react';
import { requestAPI } from '../api';

const RequestsList = ({ user }) => {
  const [requests, setRequests] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchRequests();
  }, []);

  const fetchRequests = async () => {
    try {
      const response = await requestAPI.getAll();
      setRequests(response.data.data);
      setLoading(false);
    } catch (error) {
      console.error('Error fetching requests:', error);
      setLoading(false);
    }
  };

  const handleStatusUpdate = async (id, status) => {
    try {
      await requestAPI.updateStatus(id, status);
      fetchRequests();
    } catch (error) {
      console.error('Error updating status:', error);
    }
  };

  const getBadgeClass = (status) => {
    switch (status) {
      case 'pending': return 'badge-pending';
      case 'approved': return 'badge-approved';
      case 'rejected': return 'badge-rejected';
      case 'returned': return 'badge-returned';
      default: return '';
    }
  };

  if (loading) return <div className="loading">Loading...</div>;

  return (
    <div className="container">
      <h1 className="mb-4">My Requests</h1>
      <div className="card">
        {requests.length === 0 ? (
          <p className="text-center text-gray">No requests found</p>
        ) : (
          <table className="table">
            <thead>
              <tr>
                <th>Equipment</th>
                <th>Quantity</th>
                <th>Borrow Date</th>
                <th>Return Date</th>
                <th>Status</th>
                {(user.role === 'admin' || user.role === 'staff') && <th>Actions</th>}
              </tr>
            </thead>
            <tbody>
              {requests.map((request) => (
                <tr key={request._id}>
                  <td>{request.equipment?.name || 'N/A'}</td>
                  <td>{request.quantity}</td>
                  <td>{new Date(request.borrowDate).toLocaleDateString()}</td>
                  <td>{new Date(request.returnDate).toLocaleDateString()}</td>
                  <td>
                    <span className={`badge ${getBadgeClass(request.status)}`}>
                      {request.status}
                    </span>
                  </td>
                  {(user.role === 'admin' || user.role === 'staff') && (
                    <td>
                      <div className="flex gap-2">
                        {request.status === 'pending' && (
                          <>
                            <button
                              className="btn btn-success"
                              onClick={() => handleStatusUpdate(request._id, 'approved')}
                            >
                              Approve
                            </button>
                            <button
                              className="btn btn-danger"
                              onClick={() => handleStatusUpdate(request._id, 'rejected')}
                            >
                              Reject
                            </button>
                          </>
                        )}
                        {request.status === 'approved' && (
                          <button
                            className="btn btn-primary"
                            onClick={() => handleStatusUpdate(request._id, 'returned')}
                          >
                            Mark Returned
                          </button>
                        )}
                      </div>
                    </td>
                  )}
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
};

export default RequestsList;
