import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { equipmentAPI, requestAPI } from '../api';

const NewRequest = () => {
  const [equipment, setEquipment] = useState([]);
  const [formData, setFormData] = useState({
    equipment: '',
    quantity: 1,
    borrowDate: '',
    returnDate: '',
    reason: ''
  });
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    fetchEquipment();
    const today = new Date().toISOString().split('T')[0];
    setFormData(prev => ({ ...prev, borrowDate: today }));
  }, []);

  const fetchEquipment = async () => {
    try {
      const response = await equipmentAPI.getAll({ available: 'true' });
      setEquipment(response.data.data);
    } catch (error) {
      console.error('Error fetching equipment:', error);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await requestAPI.create(formData);
      setSuccess('Request submitted successfully');
      setTimeout(() => navigate('/requests'), 1500);
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to create request');
    }
  };

  return (
    <div className="container" style={{ maxWidth: '600px' }}>
      <h1 className="mb-4">New Equipment Request</h1>
      <div className="card">
        {error && <div className="alert alert-error">{error}</div>}
        {success && <div className="alert alert-success">{success}</div>}
        
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label className="form-label">Equipment</label>
            <select
              className="form-control"
              value={formData.equipment}
              onChange={(e) => setFormData({ ...formData, equipment: e.target.value })}
              required
            >
              <option value="">Select equipment</option>
              {equipment.map((item) => (
                <option key={item._id} value={item._id}>
                  {item.name} (Available: {item.available})
                </option>
              ))}
            </select>
          </div>

          <div className="form-group">
            <label className="form-label">Quantity</label>
            <input
              type="number"
              className="form-control"
              value={formData.quantity}
              onChange={(e) => setFormData({ ...formData, quantity: e.target.value })}
              min="1"
              required
            />
          </div>

          <div className="form-group">
            <label className="form-label">Borrow Date</label>
            <input
              type="date"
              className="form-control"
              value={formData.borrowDate}
              onChange={(e) => setFormData({ ...formData, borrowDate: e.target.value })}
              required
            />
          </div>

          <div className="form-group">
            <label className="form-label">Return Date</label>
            <input
              type="date"
              className="form-control"
              value={formData.returnDate}
              onChange={(e) => setFormData({ ...formData, returnDate: e.target.value })}
              min={formData.borrowDate}
              required
            />
          </div>

          <div className="form-group">
            <label className="form-label">Reason</label>
            <textarea
              className="form-control"
              value={formData.reason}
              onChange={(e) => setFormData({ ...formData, reason: e.target.value })}
              rows="3"
              required
            />
          </div>

          <div className="flex gap-2">
            <button type="submit" className="btn btn-primary">
              Submit Request
            </button>
            <button
              type="button"
              className="btn btn-secondary"
              onClick={() => navigate('/equipment')}
            >
              Cancel
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default NewRequest;
