import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { equipmentAPI } from '../api';

const EquipmentList = () => {
  const [equipment, setEquipment] = useState([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState('');
  const [category, setCategory] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    fetchEquipment();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [search, category]);

  const fetchEquipment = async () => {
    try {
      const params = {};
      if (search) params.search = search;
      if (category) params.category = category;
      params.available = 'true';

      const response = await equipmentAPI.getAll(params);
      setEquipment(response.data.data);
      setLoading(false);
    } catch (error) {
      console.error('Error fetching equipment:', error);
      setLoading(false);
    }
  };

  if (loading) return <div className="loading">Loading...</div>;

  return (
    <div className="container">
      <div className="flex justify-between items-center mb-4">
        <h1>Available Equipment</h1>
        <button className="btn btn-primary" onClick={() => navigate('/request/new')}>
          New Request
        </button>
      </div>

      <div className="card">
        <div className="grid grid-2 gap-2 mb-4">
          <div className="form-group">
            <input
              type="text"
              className="form-control"
              placeholder="Search equipment..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
          </div>
          <div className="form-group">
            <select
              className="form-control"
              value={category}
              onChange={(e) => setCategory(e.target.value)}
            >
              <option value="">All Categories</option>
              <option value="Sports">Sports</option>
              <option value="Lab">Lab</option>
              <option value="Electronics">Electronics</option>
              <option value="Musical">Musical</option>
              <option value="Other">Other</option>
            </select>
          </div>
        </div>

        <div className="grid grid-3">
          {equipment.map((item) => (
            <div key={item._id} className="card">
              <h3>{item.name}</h3>
              <p className="text-sm text-gray">{item.category}</p>
              <p className="text-sm">{item.description}</p>
              <div className="mt-4">
                <div className="flex justify-between text-sm">
                  <span>Condition: {item.condition}</span>
                  <span>Available: {item.available}/{item.quantity}</span>
                </div>
              </div>
            </div>
          ))}
        </div>

        {equipment.length === 0 && (
          <p className="text-center text-gray">No equipment found</p>
        )}
      </div>
    </div>
  );
};

export default EquipmentList;
